/**
 * API Configuration for Open Notebook Backend
 * This file handles all communication with the Open Notebook API backend
 */

// Backend API configuration
const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5055';

export interface Notebook {
  id: string;
  name: string;
  description?: string;
  created: string;
  updated: string;
}

export interface Source {
  id: string;
  title: string;
  topics?: string[];
  asset?: {
    file_path?: string;
    url?: string;
  };
  full_text?: string;
  embedded_chunks: number;
  created: string;
  updated: string;
  message?: string;
}

export interface Book {
  id: string;
  title: string;
  author?: string;
  fileType: 'pdf' | 'epub' | 'txt';
  uploadDate: string;
  filePath: string;
  coverPath?: string;
  fileSize: number;
  content?: string;
  currentPage?: number;
}

/**
 * Ensure a notebook exists before uploading
 * This is the key fix for the 500 error issue
 */
export async function ensureNotebookExists(): Promise<string> {
  try {
    // First, try to get existing notebooks
    const response = await fetch(`${API_BASE_URL}/api/notebooks`);
    const notebooks = await response.json();
    
    if (notebooks.length > 0) {
      // Use the first available notebook
      console.log('Using existing notebook:', notebooks[0].id);
      return notebooks[0].id;
    }
    
    // If no notebooks exist, create one automatically
    console.log('No notebooks found, creating new one...');
    const createResponse = await fetch(`${API_BASE_URL}/api/notebooks`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        name: 'Book Uploads',
        description: 'Auto-created notebook for book uploads'
      })
    });
    
    if (!createResponse.ok) {
      throw new Error('Failed to create notebook');
    }
    
    const newNotebook = await createResponse.json();
    console.log('Created new notebook:', newNotebook.id);
    return newNotebook.id;
    
  } catch (error) {
    console.error('Error ensuring notebook exists:', error);
    throw new Error('Could not create or find a notebook for book upload');
  }
}

/**
 * Upload a book to the Open Notebook backend
 * This replaces the local API call with the backend API
 */
export async function uploadBook(file: File, title?: string): Promise<Source> {
  try {
    // Ensure we have a valid notebook ID
    const notebookId = await ensureNotebookExists();
    
    // Create form data
    const formData = new FormData();
    formData.append('notebook_id', notebookId);
    formData.append('file', file);
    
    if (title) {
      formData.append('title', title);
    }
    
    // Upload the book to the backend
    const response = await fetch(`${API_BASE_URL}/api/sources`, {
      method: 'POST',
      body: formData
    });
    
    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.detail || 'Failed to upload book');
    }
    
    const result = await response.json();
    console.log('Book uploaded successfully:', result);
    return result;
    
  } catch (error) {
    console.error('Error uploading book:', error);
    throw error;
  }
}

/**
 * Get all notebooks from the backend
 */
export async function getNotebooks(): Promise<Notebook[]> {
  try {
    const response = await fetch(`${API_BASE_URL}/api/notebooks`);
    if (!response.ok) {
      throw new Error('Failed to fetch notebooks');
    }
    return await response.json();
  } catch (error) {
    console.error('Error fetching notebooks:', error);
    throw error;
  }
}

/**
 * Create a new notebook
 */
export async function createNotebook(name: string, description?: string): Promise<Notebook> {
  try {
    const response = await fetch(`${API_BASE_URL}/api/notebooks`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        name,
        description
      })
    });
    
    if (!response.ok) {
      throw new Error('Failed to create notebook');
    }
    
    return await response.json();
  } catch (error) {
    console.error('Error creating notebook:', error);
    throw error;
  }
}

/**
 * Get all sources/books from a notebook
 */
export async function getSources(notebookId: string): Promise<Source[]> {
  try {
    const response = await fetch(`${API_BASE_URL}/api/sources?notebook_id=${notebookId}`);
    if (!response.ok) {
      throw new Error('Failed to fetch sources');
    }
    return await response.json();
  } catch (error) {
    console.error('Error fetching sources:', error);
    throw error;
  }
}

/**
 * Convert Source from backend to Book format for frontend compatibility
 */
export function sourceToBook(source: Source): Book {
  return {
    id: source.id,
    title: source.title,
    author: 'Unknown', // Backend doesn't have author field
    fileType: 'txt', // Default, could be determined from file_path
    uploadDate: source.created,
    filePath: source.asset?.file_path || '',
    coverPath: '', // Backend doesn't generate covers
    fileSize: 0, // Backend doesn't track file size
    content: source.full_text || '',
    currentPage: 1
  };
}

/**
 * Health check for the backend API
 */
export async function healthCheck(): Promise<boolean> {
  try {
    const response = await fetch(`${API_BASE_URL}/health`);
    return response.ok;
  } catch (error) {
    console.error('Backend health check failed:', error);
    return false;
  }
}
